.container {
  display: flex;
  flex-direction: column;
  width: 100%;
  align-items: center;
}

.main-title {
  text-decoration: underline;
}

input {
  border-radius: 7px;
  outline: none;
  border: 1px solid black;
  padding: 5px;
}

.title {
  text-decoration: underline;
}

.phonebook {
  background-color: rgb(223, 222, 222);
  border: 1px solid black;
  border-radius: 20px;
  padding: 20px;
  text-align: center;
}
